.button {
    font-size: 18px;
    padding: 7px 15px 3px;
    text-transform: uppercase;
    background-color: #b4afb6;
    border-radius: 8px;
    color: white;
    transition: background-color 0.3s ease 0s;
    width: 50%;
}

.button:hover {
    background-color: #a099a3;
}

.btn-block {
    display: flex;
    justify-content: center;
}