.row {
    display: flex;
    justify-content: center;
    gap: 25px;
    margin-bottom: 50px;
}

@media (max-width: 767px) {
    .row {
        flex-direction: column;
        /* Изменяет направление флексов на колонку */
        align-items: center;
        /* Центрирует элементы по горизонтали */
    }
}