.form-row {
    display: flex;
    flex-direction: column;
    gap: 15px;
    align-items: center;
}
.label {
    font-size: 16px;
}
.input {
    height: 35px;
    font-size: 25px;
    padding-left: 15px;
    border: 2px solid #EFB3B5;
    border-radius: 8px;
    padding-top: 5px;
    padding-bottom: 5px;
}
.input:focus {

    outline: 1px solid #fb8185;
  
}

@media (max-width: 375px) {
    .input {
        width: 80%;
    }
}