.popup-result {
    background: #f8f1f9d4;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
}

.popup-result__body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-direction: column;
}

.popup-result__item {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
    border-radius: 8px;
    border: 2px solid #bcbaba;
    padding: 10px;
    margin-bottom: 10px;
    background-color: white;
    max-width: 400px;
}

@media (max-width: 530px) {
    .popup-result__item {

        max-width: 300px;
    }
}

.popup-result__block {}

.popup-result__img {
    aspect-ratio: 25 / 14;
    /* Соотношение сторон 16:9 */
    width: 100%;
    /* Ширина изображения займет всю ширину контейнера */
    height: auto;
    /* Высота автоматически рассчитывается на основе соотношения сторон */
    border-radius: 8px;
    margin-bottom: 25px;
}

.popup-result__res {
    text-align: center;
    margin-bottom: 10px;
    line-height: 1.5;
    font-weight: 800;
}

.popup-result__text {
    text-align: center;
    margin-bottom: 10px;
    line-height: 1.02;
}

.popup-result__btn {
    width: 80%;
    margin: 0 auto;
    font-size: 16px;
    background-color: #e1e0e0;
    transition: background-color 0.3s ease 0s;
    text-transform: uppercase;
    padding-top: 5px;
    padding-bottom: 3px;
    border-radius: 8px;
}