.popup {
    background: #f8f1f9d4;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
}

.hidden {
    display: none;
}
.popup__body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-direction: column;
    padding-left: 15px;
    padding-right: 15px;
}

.popup__item {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
    border-radius: 8px;
    border: 2px solid #bcbaba;
    padding: 10px;
    margin-bottom: 10px;
    background-color: white;
    max-width: 450px;
}

.popup__title {
    font-size: 16px;


}

.popup__text {
    font-size: 14px;
    max-width: 400px;
}